.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.compass {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background-color: #eee;
  position: relative;
  margin-bottom: 20px;
}

.arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
  width: 5px;
  height: 100px;
  background-color: black;
}

.distance {
  font-size: 20px;
}

.label {
  position: absolute;
  font-size: 16px;
  transform-origin: 50% 50%;
}

.north {
  transform: translate(100px, 0);
}

.east {
  transform: translate(200px, 100px) rotate(90deg);
}

.south {
  transform: translate(100px, 200px) rotate(180deg);
}

.west {
  transform: translate(0, 100px) rotate(270deg);
}
